export type UserType = {
    _id: string
    name: string
}

export const pureAddUserCallback = (name: string, setUsers: Dispatch<SetStateAction<Array<UserType>>>, users: Array<UserType>) => {
    const user = { _id: v1(), name };
    setUsers([...users, user]);
}

const HW3 = () => {
    const [users, setUsers] = useState<Array<UserType>>([]);

    const addUserCallback = (name: string) => {
        pureAddUserCallback(name, setUsers, users)
    }

    return (
        <div id={'hw3'}>
            <div className={s2.hwTitle}>Homework #3</div>
            {/*    ( )*/}

            <div className={s2.hw}>
                <GreetingContainer
                    users={users}
                    addUserCallback={addUserCallback}
                />
            </div>
        </div>
    )
}

export default HW3;